import {
  Fu0,
  Gin0,
  Gyoku0,
  Hisha0,
  Kaku0,
  Kei0,
  Kin0,
  Kyou0,
  NariGin0,
  NariKei0,
  NariKyou0,
  Piece,
  Ryu0,
  To0,
  Uma0,
} from '../model/shogi/Piece'

const rows = ['', '', '', '', '', '', '', '', '']
const columns = ['', '', '', '', '', '', '', '', '']

export const rowString = (r: number) => rows[r]
export const columnString = (c: number) => columns[c]

export const pieceString = (piece: Piece) => {
  const p = Math.abs(piece)

  if (p === Fu0) return ''
  if (p === Kyou0) return ''
  if (p === Kei0) return ''
  if (p === Gin0) return ''
  if (p === Kin0) return ''
  if (p === Kaku0) return ''
  if (p === Hisha0) return ''
  if (p === Gyoku0) return ''
  if (p === To0) return ''
  if (p === NariKyou0) return ''
  if (p === NariKei0) return ''
  if (p === NariGin0) return ''
  if (p === Uma0) return ''
  if (p === Ryu0) return ''

  throw new Error('Failed to ident piece name. received: ' + piece)
}
