import {
  Fu0,
  Gin0,
  Gyoku0,
  Hisha0,
  Kaku0,
  Kei0,
  Kin0,
  Kyou0,
  NariGin0,
  NariKei0,
  NariKyou0,
  Piece,
  Ryu0,
  To0,
  Uma0,
} from '../../../model/shogi/Piece'
import { Turn } from '../../../model/shogi/Turn'
import { Parser, rep, s, ss, success, repsep } from '../parser'

export function piece(turn: Turn): Parser<Piece> {
  return Parser(s => {
    if (s.length === 0) return null
    const st = (t: string) => s.startsWith(t)
    const suc = (p: Piece, n: number) => success(p * turn, s.substring(n))
    if (st('')) return suc(Fu0, 1)
    if (st('')) return suc(Kyou0, 1)
    if (st('')) return suc(Kei0, 1)
    if (st('')) return suc(Gin0, 1)
    if (st('')) return suc(Kin0, 1)
    if (st('')) return suc(Gyoku0, 1)
    if (st('')) return suc(Kaku0, 1)
    if (st('')) return suc(Hisha0, 1)
    if (st('')) return suc(To0, 1)
    if (st('')) return suc(NariKyou0, 2)
    if (st('')) return suc(NariKyou0, 1)
    if (st('')) return suc(NariKei0, 2)
    if (st('')) return suc(NariKei0, 1)
    if (st('')) return suc(NariGin0, 2)
    if (st('')) return suc(NariGin0, 1)
    if (st('')) return suc(Uma0, 1)
    if (st('') || st('')) return suc(Ryu0, 1)
    return null
  })
}

export const row: Parser<number> = Parser(input => {
  const s = input[0]
  const next = () => input.substring(1)
  if (s === '' || s === '' || s === '1') return success(0, next())
  if (s === '' || s === '' || s === '2') return success(1, next())
  if (s === '' || s === '' || s === '3') return success(2, next())
  if (s === '' || s === '' || s === '4') return success(3, next())
  if (s === '' || s === '' || s === '5') return success(4, next())
  if (s === '' || s === '' || s === '6') return success(5, next())
  if (s === '' || s === '' || s === '7') return success(6, next())
  if (s === '' || s === '' || s === '8') return success(7, next())
  if (s === '' || s === '' || s === '9') return success(8, next())
  return null
})

export const column: Parser<number> = Parser(input => {
  const s = input[0]
  const next = () => input.substring(1)
  if (s === '' || s === '1') return success(0, next())
  if (s === '' || s === '2') return success(1, next())
  if (s === '' || s === '3') return success(2, next())
  if (s === '' || s === '4') return success(3, next())
  if (s === '' || s === '5') return success(4, next())
  if (s === '' || s === '6') return success(5, next())
  if (s === '' || s === '7') return success(6, next())
  if (s === '' || s === '8') return success(7, next())
  if (s === '' || s === '9') return success(8, next())
  return null
})

export const digit: Parser<number> = Parser(input => {
  const s = input[0]
  const next = input.substring(1)
  if (s === '' || s === '0') return success(0, next)
  if (s === '' || s === '1') return success(1, next)
  if (s === '' || s === '2') return success(2, next)
  if (s === '' || s === '3') return success(3, next)
  if (s === '' || s === '4') return success(4, next)
  if (s === '' || s === '5') return success(5, next)
  if (s === '' || s === '6') return success(6, next)
  if (s === '' || s === '7') return success(7, next)
  if (s === '' || s === '8') return success(8, next)
  if (s === '' || s === '9') return success(9, next)
  return null
})

export const integer: Parser<number> = rep(digit)
  .failif(arr => arr.length === 0)
  .map(arr => {
    let n = 0
    for (let i = 0; i < arr.length; i++) {
      n *= 10
      n += arr[i]
    }
    return n
  })

// hh:mm:ss 
// ssmm:ss  OK
export const time: Parser<number> = repsep(integer, s(':'))
  .failif(n => n.length === 0 || n.length > 3)
  .map(nums => {
    let sec: number = 0
    for (let i = 0; i < nums.length; i++) {
      sec *= 60
      sec += nums[i]
    }
    return sec
  })

// 
// 
// `` 
// hms 
//  0 
const each = ss('').fallback('')
const hour = integer.left(ss('')).fallback(0)
const minutes = integer.left(ss('')).fallback(0)
const seconds = integer.left(ss('')).fallback(0)
export const totalTime: Parser<number> = each
  .right(hour)
  .comb(minutes)
  .comb(seconds)
  .map(([[h, m], s]) => h * 3600 + m * 60 + s)
